.video-banner {
  background-image: url("https://biggica-sites.s3.amazonaws.com/fongs-hung-ga/head-banner.jpg");
  background-position: 50%;
  background-size: cover;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden; }
  .video-banner:before {
    content: '';
    background: rgba(0, 0, 0, 0.75);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2; }
    @media (min-width: 1026px) {
      .video-banner:before {
        background: rgba(0, 0, 0, 0.65); } }
  .video-banner iframe {
    display: none;
    width: 100vw;
    height: 56.25vw;
    min-height: 100vh;
    min-width: 177.77vh;
    position: absolute;
    pointer-events: none;
    top: 50%;
    left: 50%;
    z-index: 1;
    border: 0;
    transform: translate(-50%, -50%); }
    @media (min-width: 1026px) {
      .video-banner iframe {
        display: block; } }

.logo {
  width: 250px;
  height: 250px;
  position: fixed;
  left: 50%;
  top: 30%;
  transform: translate(-50%, -25%); }
  @media (min-width: 600px) {
    .logo {
      width: 300px;
      height: 300px;
      top: 38%;
      font-size: 21px;
      line-height: 31px; } }

.description {
  font-size: 18px;
  line-height: 28px;
  color: #fff;
  position: fixed;
  left: 50%;
  top: 53%;
  transform: translate(-50%, -25%);
  text-align: center;
  width: 100%;
  padding: 0 20px;
  max-width: 600px; }
  @media (min-width: 600px) {
    .description {
      top: 65%;
      font-size: 21px;
      line-height: 31px; } }
